.App {
  text-align: center;


  /* height: page; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.Header-contents{
  display: flex;
  justify-content: space-between ;
  margin: auto 10px;
}

.App-body {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  min-height: 91vh;

}

.App-link {
  color: #61dafb;
}

.App-footer {
  background-color: orange;
  padding: 15px 15px;
  display: flex;
  justify-content: space-around;
}

.Navigator{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto 10px;


}

h2{
  color: black;
}

a {
  width: 100%;


}

a:link{
  text-decoration: none;
  /* color: black; */
}

a:visited{
  text-decoration: none;
  color: black
}

a:active{
  color: white
}

a:hover{
  color: white;
  transition: all 250ms ease-in-out;
}

.body-divs{
  margin: 30px 30px;
}

.body-texts{
  font-size: 18px;
  margin: 25px;
  
}

.logos{
  display: flex;
  justify-content: space-around;
  width: 25%
}

.skill-lists{
  display: flex;
  justify-content: space-evenly;
}

.list-divs{
  text-align: left;
}

.contact-form{
  align-items: center;

}

.label-divs{
  margin: 25px;
  display: flex;
  justify-content: space-between;
}


input{
  width: 75%;
}

textarea{
  width: 75%;
}

.container_wrapper{
  margin: 0px auto;
  width: 40%;
  border-radius: 5px;
  position: relative;
}

.app-description{
  margin-left: 20%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
}

.app_image{

}

.container--picture{
  width: 100%
}

.container--overlay{
  position: absolute;
  top: 0 ;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container--overlay:hover{
  opacity: 1;
  transition: all 250ms ease-in-out;
}

.container---blurb{
  color: white;
  margin: 0px auto;
  width: 80%
}

.App-header{
  background-color: Orange;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 99999;
}


.app-box{
  display: flex;
  margin: 0 auto;
  margin-top: 5px;
  padding-bottom: 15px;
  border-bottom: 3px solid orange;
  width: 80%;

}



.links{
  color: white;
  font-family: copperplate;
  font-size: 25px;
}

h1, h3{
  font-family: fantasy;
}

li {
  font-size: 20px;
}

